<template>
    <div>
        <!-- 顶部logo -->
        <div id="head">
            <div class="head-box">
                <div class="header-title">
                    <h2>西南石油大学学位英语考试报名</h2>
                </div>
                <div class="header-logo-right">
                    <span>客服电话</span>
                    <span>010-XXXX</span>
                </div>
            </div>
        </div>
        <div class="content">
            <router-view></router-view>
        </div>
        <!-- 底部 -->
      <Footer></Footer>
    </div>
</template>

<script>
import Footer from '@/views/Layout/Footer.vue'
export default {
    components:{
        Footer
    },
    data(){
        return{
        ruleForm:{
            account:'',
            password:''
        },
        rules:{
            account:[
            { required:true,message:'请输入用户账号',trigger:'blur'}
            ],
            password:[
            { required:true,message:'请输入用户密码',trigger:'blur'},
            { min:6,max:12,message:'密码长度为6至16',trigger:'blur'}
            ]
        },
        checked:false
        }
    },
    methods:{
        submitForm(formVal){
            let that=this;
            this.$refs[formVal].validate((valid)=>{
            if(valid){
                that.$api.login({
                    account:that.ruleForm.account,
                    password:that.ruleForm.password,
                    rememberMe:that.checked
                }).then(res=>{
                    if(res.err_code === 101){
                    that.$message('输入密码错误,请重新输入');
                    that.ruleForm.password='';
                }else if(res.err_code === 200){
                        localStorage.setItem('TOKEN','Bearer '+res.data.token);
                        that.$message('登录成功');
                        that.$router.push('/login/info');
                }else{
                        that.$message('登录失败');
                    }
                })
                }else{
                    return that.$message('输入存在错误,无法登录');
                }
            })
        },
        toRegister(){
            this.$router.push('/register')
        }
    }
}
</script>

<style lang="scss">
    #head{
        width: 100%;
        background: #1f499f;
        .head-box{
            width: 1000px;
            height: 80px;
            margin: 0 auto;
            line-height: 80px;
            background: url('../../../assets/headerbg.jpg') no-repeat bottom right;
            background-size: cover;
            
            .header-title{
                float: left;
                color: #fff;
            }

            .header-logo-right{
                color: #fff;
                float: right;
            }
        }
    }

    .content{
        width: 1000px;
        margin: 0 auto;
        background: #f5f5f5;
    }
</style>